:root {
  --light-blue: #00acdc;
  --column-count: 4;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, Arial, sans-serif;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
}

.header {
  box-sizing: border-box;
  text-align: center;
  justify-content: space-between;
  align-items: center;
  width: 100%;
  height: 60px;
  padding-left: 50px;
  padding-right: 50px;
  display: flex;
}

.header-logo {
  display: block;
}

.nav__list {
  padding-inline-start: 0;
  display: inline;
}

.nav__item {
  display: inline-block;
}

.nav__link {
  color: #060b35;
  text-transform: uppercase;
  height: 60px;
  margin-left: 17px;
  line-height: 60px;
  text-decoration: none;
  display: block;
}

.is-active:after {
  content: "";
  background-color: var(--light-blue);
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: inline-block;
  position: absolute;
  bottom: 0;
  left: 0;
}

.is-active {
  color: var(--light-blue);
  height: 60px;
  margin-left: 0;
  position: relative;
}

.nav__link:hover, .nav__link:focus {
  color: var(--light-blue);
}

.main {
  grid-template-columns: repeat(var(--column-count), 200px);
  grid-template-rows: repeat(2, 408px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 320px) {
  .main {
    --column-count: 1;
  }
}

@media (width >= 488px) {
  .main {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .main {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .main {
    --column-count: 4;
  }
}

.card {
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: fit-content;
  transition: all .3s;
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 32px 19px 37px;
}

.card__title {
  color: #060b35;
  width: 166px;
  margin: 0 16px 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  width: fit-content;
  margin: 0 84px 16px 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__rating {
  justify-content: space-between;
  width: 166px;
  height: 16px;
  margin: 0 16px 24px;
  display: flex;
}

.card__reviews {
  color: #060b35;
  align-self: center;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price-block {
  justify-content: space-between;
  align-items: center;
  width: 166px;
  height: 18px;
  margin: 0 16px 16px;
  display: flex;
}

.card__price-title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-amount {
  color: #060b35;
  align-self: center;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  align-items: center;
  width: 166px;
  height: 40px;
  margin: 0 16px 16px;
}

.card__buy-button-link {
  text-transform: uppercase;
  color: #fff;
  align-self: center;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
}

.card__buy-button:hover {
  background-color: #fff;
}

.card__buy-button:hover .card__buy-button-link {
  color: #00acdc;
  font-size: 14px;
  line-height: 16px;
}

.card:hover {
  transform: scale(1.2);
}

.card .card:hover .card__title {
  color: #34568b;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  display: inline-block;
}

.stars .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
